.sidebar {
    position: absolute;

    width: 317px;
    height: 720px;
    background-color: #FFF;
    box-shadow: 0px 10px 40px 0px rgba(29, 33, 38, 0.13), 0px 1px 2px 0px rgba(29, 33, 38, 0.10);
    transform: translateX(-100%);
    transition: 1s;

    display: flex;
    flex-direction: column;
    z-index: 99;
}

.sidebar.open {
    transform: translateX(-0);
}

.background {
    background-color: #000;
    opacity: 0.3;
    width: 100%;
    height: 720px;
    z-index: 1;
    position: absolute;
}

.nav {
    display: flex;
    flex-direction: column;
    gap:45px;
    margin-left: 70px;
    margin-top: 24px;
}

.nav a {
    color: #000;
    font-family: Montserrat, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration: none;
}

.nav a:hover {
    color: #58ACFF;
    font-family: Montserrat, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration: none;
}

.nav a.active {
    color: #0059B2;
    font-family: Montserrat, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    text-decoration-line: underline;

}
.close {
    display: flex;
    margin-left: 89%;
    margin-top: 16px;
    width: 24px;
    height: 24px;
    justify-content: center;
    align-items: center;
    flex-shrink: 0;
    background-color: unset;
    border: 0;
    cursor: pointer;
}

